.container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.main{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh; 
}
.content{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 50px 50px 70px 50px;
    border: none;
    border-radius: 20px;
    box-shadow:0 0 50px 15px #875695;
    ;
}
.form-group{
    display: flex;
    padding: 10px;
    flex-direction: column;
}
input{
    padding-left: 10px;
    margin: 3%;
    width: 290px;
    height: 30px;
}
button{
    width: 30px;
    height: 30px;
    margin: 10px;
    background-color: black;
    color: white;

}
.btn-group{
    width: 310px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-left: 15px;
}
p{
    text-align: center;
    padding-top: 10%;
}
.error{
    color: red;
}
.success{
    color: green;
}
.res{
    text-align: center;
}
@media screen and (min-width: 320px) and (max-width: 480px) {
    /* Your CSS code here */
    h1{
        text-align: center;
    }
    .main{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 253px;
    }
    .content{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 55%;
        height: 250px;
    }
    input{
        padding-left: 10px;
        margin: 3%;
        width: 150px;
        height: 30px;
    }
    .btn-group{
        width: 200px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding-left: 15px;
    }
  }